.header{padding:15px 0}.header-nav{padding:15px 0;justify-content:center;align-items:center}@media (min-width: 768px){.header-nav{padding-top:0;justify-content:flex-end}}.header-menu{background:#fff}.header-menu-small{padding-top:40px;top:100px;width:280px;height:100%;z-index:1;top:0;transition:all 0.5s ease-in-out;position:fixed}.header-menu-default{display:flex}.header-menu-link{color:#2f281e;font-size:14px;text-decoration:none;padding:15px;display:block;transition:all 0.3s ease-in-out}.header-menu-link:hover{background:#f90;color:#fff;transition:all 0.3s ease-in-out}.show{right:0;transition:all 0.5s ease-in-out}.hide{transition:all 0.5s ease-in-out;right:-999px}.overlay{background:rgba(0,0,0,0.2);transition:all 0.5s ease-in-out;position:fixed;top:0;left:0;right:0;bottom:0;z-index:0}.header-menu-open{position:absolute;top:-28px;right:15px}@media (min-width: 768px){.header-menu-open{top:10px}}.header-menu-close{position:absolute;top:10px;right:10px}@media (min-width: 768px){.header-wrapper-authenticate{margin-right:60px}}@media (min-width: 992px){.header-wrapper-authenticate{margin-right:0;margin-left:60px}}.header-btn{background:none;border:0;border-radius:5px;padding:10px 30px}.header-btn-signin{border:1px solid #bcd0e5;color:#173a56;margin-right:10px}.header-btn-signup{background:#f90;border:1px solid #f90;color:#fff}

